import Vue from 'vue'
import router from './router.js'
import store from "./store.js"

import VueResource from "vue-resource"
Vue.use(VueResource)


// Vue 
Vue.config.productionTip = false
Vue.http.options.root = 'http://127.0.0.1:3000';
// 
Vue.http.options.emulateJSON = true;



//  Element-UI
import { Icon, Button, Card, Loading, Select, Option, InputNumber, FormItem, Form, Input } from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';

Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 }

Vue.use(Icon) //  Vue ElementUI
Vue.use(Button)
Vue.use(Card)
Vue.use(Select)
Vue.use(Option)
Vue.use(InputNumber)
Vue.use(FormItem)
Vue.use(Form)
Vue.use(Input)
Vue.prototype.$loading = Loading.service




// Swiper
import Swiper from "swiper"
import "swiper/dist/css/swiper.css"
Vue.prototype.$Swiper = Swiper



//  less 
import "./less/base.less"
// 
import "./lib/fontAwesome/css/font-awesome.css"




// Mint-UI
import { Header, Tabbar, TabItem, Search, Cell, Toast, Loadmore } from "mint-ui"
import 'mint-ui/lib/style.css'




//  Mint-UI 
Vue.component(Header.name, Header)
Vue.component(Tabbar.name, Tabbar)
Vue.component(TabItem.name, TabItem)
Vue.component(Search.name, Search)
Vue.component(Cell.name, Cell)
Vue.component(Loadmore.name, Loadmore)

Vue.prototype.$Toast = Toast





import App from './App.vue'



new Vue({
  router,
  store,
  render: c => c(App)
}).$mount('#app')
