<template>

  <div id="categoryContainer">


    <!--  -->
    <div class="swiper-container category-left category">
      <!--  -->
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <!--DOM-->
          <ul>
            <li :class="item.id === id? 'active': ''" @click="changeCategory(item.id)" v-for="item in categoryList"
                :key="item.id"><a href="javascript:void(0)">{{ item.categoryName }}</a></li>
          </ul>
        </div>
      </div>

      <!--  -->
      <div class="swiper-scrollbar">
      </div>
    </div>


    <div class="swiper-container category-right category">
      <!--  -->
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <ul>
            <li v-for="item in secondCategory" :key="item.id">
              <a href="javascript:void(0)">
                <img :src="'http://localhost:3000'+ item.brandLogo" alt="">
                <p>{{ item.brandName }}</p>
              </a>
            </li>
          </ul>
        </div>
      </div>

      <!--  -->
      <div class="swiper-scrollbar">
      </div>
    </div>


  </div>

</template>

<script>
  export default {
    name: "categoryContainer",
    data() {
      return {
        categoryList: [],
        secondCategory: [],
        id: 1
      }
    },
    methods: {
      getCategoryList() {
        this.$http.get("category/queryTopCategory").then((result) => {
          console.log(this.categoryList = result.body.rows);
        })
      },
      changeCategory(id) {
        this.id = id
        this.$http.get("category/querySecondCategory", {
          params: {id}
        },).then((result) => {
          this.secondCategory = result.body.rows
        })

      }
    },
    created() {
      this.getCategoryList()
      this.changeCategory(1)
    },
    mounted() {
      new this.$Swiper(".swiper-container.category", {
        direction: "vertical",
        freeMode: true,
        slidesPerView: 'auto',
        mousewheelControl: true,
        roundLengths: true, //
        scrollbar: {
          el: '.swiper-scrollbar',
        },
      });
    },

  }
</script>


<style scoped lang="less">
  @main-color: #f10215;
  #categoryContainer {
    height: 100%;
    display: flex;

    .category-left {
      width: 0.8rem;
      position: relative;

      ul {
        /*     */
        min-height: 5.77rem;

        li {
          a {
            display: block;
            color: #666;
            height: 0.45rem;
            text-align: center;
            line-height: 0.45rem;
            font-size: 0.14rem;
            background-color: #fff;
            border-bottom: 1px solid #ccc;
          }

          &.active {
            a {
              transition: all .5s;
              background-color: #eee;
              color: @main-color;
            }
          }
        }
      }
    }

    .category-right {
      flex: 1;
      /*  padding */
      /* padding: 10px; */
      position: relative;

      ul {
        display: flex;
        flex-wrap: wrap;
        padding: 10px;
        min-height: 5.77rem;
        /*   align-content:stretch 
        flex-start */
        align-content: flex-start;

        li {
          width: 33.33%;

          a {
            display: block;
            background-color: #fff;
            color: black;

            img {
              width: 100%;
            }

            p {
              font-size: 0.14rem;
              text-align: center;
            }
          }
        }
      }
    }

    .swiper-container.category {
      height: 100%;

      .swiper-slide {
        height: auto;

      }
    }

  }
</style>