<template>
  <div id="goodsList">
    <!--   id -->
    <div class="swiper-container goodsList" id="refreshContainer">
      <!--  -->
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <!--  -->
          <!--  -->
          <mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" :bottom-method="loadBottom"
                       :bottom-all-loaded="allLoaded" @bottom-status-change="handleBottomChange" ref="loadmore">
            <div class="product-list">
              <div class="title">
                <!-- ul>li*4>a[href="#"]{}>i.fa.fa-angle-down -->
                <ul>
                  <li class="active"><a href="javascript:void(0)"><i class="fa fa-angle-down"></i></a></li>
                  <!--  1     2  1 -->
                  <!-- data-sort-type="price" () -->
                  <li @click="forPrice"><a href="javascript:void(0)" data-sort="1" data-sort-type="price"><i
                      class="fa fa-angle-down"></i></a></li>
                  <li @click="forNum"><a href="javascript:void(0)" data-sort="1" data-sort-type="num"><i
                      class="fa fa-angle-down"></i></a>
                  </li>
                  <li><a href="javascript:void(0)"><i class="fa fa-angle-down"></i></a></li>
                </ul>
              </div>
              <!-- ul>li*8>a[href="#"]>img[src="../../assets/shop/images/brand$.png"] -->
              <div class="content">
                <ul>

                  <li v-for="item in productList" :key="item.id + Math.random()">
                    <a href="#" class="product">
                      <img class="product-img" :src="'http://127.0.0.1:3000' + item.pic[0].picAddr" alt="">
                      <p class="product-name">{{ item.proName }}</p>
                      <p class="product-price">
                        <span>{{ '$ ' + item.price }}</span>
                        <del>{{ '$ ' + item.oldPrice }}</del>
                      </p>
                      <p class="product-buy">
                        <router-link :to="'/goodsInfo/'+item.id" tag="div"><el-button size="small" round type="primary" plain></el-button></router-link>
                      </p>
                    </a>
                  </li>

                </ul>
              </div>
            </div>
            <div slot="top" class="mint-loadmore-top">
              <span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }"></span>
              <span v-show="topStatus === 'loading'"><i class="el-icon-loading"></i></span>
            </div>
            <div slot="bottom" class="mint-loadmore-bottom">
              <span v-show="bottomStatus !== 'loading'" :class="{ 'rotate': bottomStatus === 'drop' }">shang</span>
              <span v-show="bottomStatus === 'loading'">Loading...</span>
            </div>
          </mt-loadmore>


        </div>
      </div>
      <!--  -->
      <div class="swiper-scrollbar">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "GoodsList",
    data() {
      return {
        topStatus: '',
        bottomStatus: '',
        page: 1,
        pageSize: 4,
        productList: [],
        allLoaded: false,
        price: true,
        num: true,
      }
    },
    methods: {
      init() {
        new this.$Swiper(".swiper-container.goodsList", {
          direction: "vertical",
          freeMode: true,
          slidesPerView: 'auto',
          mousewheelControl: true,
          roundLengths: true, //
          scrollbar: {
            el: '.swiper-scrollbar',
          },
        })
      },
      // buy(id) {
      //   this.$router.replace({name: "goodsInfo", params: {id}})
      // },
      loadBottom() {
        this.getProduct().then((result) => {
            if (result.body.data.length == 0) {
              this.allLoaded = true
              this.$refs.loadmore.onBottomLoaded()
              this.$Toast({
                message: "",
                duration: 1500
              })
            }else {
              this.productList = this.productList.concat(result.body.data)
              this.page++
            }
          })
      },
      loadTop() {
        this.page = 1
        this.getProduct().then((result) => {
            this.productList = result.body.data
            this.$refs.loadmore.onTopLoaded()
        })
      },
      handleTopChange(status) {
        this.topStatus = status;
      },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      getProduct() {
        return new Promise((resolve) => {
          this.$http.get("product/queryProduct", {
            params: {
              page: this.page,
              pageSize: this.pageSize,
              proName: this.$route.params.search
            }
          })
              .then(result => {
                console.log(result.body.data);
                resolve(result)
              })
        })
      },
      forPrice() {
        this.page = 1
        this.$http.get("product/queryProduct", {
          params: {
            price: this.price ? 1 : 2,
            page: this.page,
            pageSize: this.pageSize,
            proName: this.$route.params.search
          }
        })
            .then(result => {
              console.log(result.body.data);
              this.productList = result.body.data
              this.price = !this.price
              this.num = true
              this.page = 1
            })


      },
      forNum() {
        this.$http.get("product/queryProduct", {
          params: {
            num: this.num ? 1 : 2, page: this.page,
            pageSize: this.pageSize,
            proName: this.$route.params.search
          }
        })
            .then(result => {
              this.productList = result.body.data
              this.num = !this.num
              this.price = true
              this.page = 1
            })
      }
    },
    created() {
      this.getProduct().then((result) => {
        if (result.body.data == []) {
          this.allLoaded = true
          this.$refs.loadmore.onBottomLoaded()
          return this.$Toast("")
        } else {
          this.productList = result.body.data
        }
      })
    },
    mounted() {
      this.init()
    }
  }
</script>

<style scoped lang="less">
  #goodsList {
    height: 100%;

    .swiper-container {
      height: 100%;

      .swiper-wrapper {
        height: 100%;

        .swiper-slide {
          height: auto;

          .product-list {
            height: 100%;
            padding-top: 10px;
            display: flex;
            align-content: space-between;
            flex-wrap: wrap;

            .title {
              width: 100%;
              border-top: solid 1px #ccc;
              border-bottom: solid 1px #ccc;

              ul {
                display: flex;

                li {
                  flex: 100%;
                  height: 40px;
                  text-align: center;
                  line-height: 40px;

                  a:active {
                    color: #f10215;
                  }
                }
              }
            }

            .content {
              width: 100%;

              ul {
                display: flex;
                flex-wrap: wrap;

                li {
                  padding-bottom: 10px;
                  margin-top: 10px;

                  border: solid 1px #ccc;
                  width: 50%;
                  text-align: center;

                  .product-name {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box; //

                    -webkit-box-orient: vertical; //

                    -webkit-line-clamp: 2; //css
                    padding-left: 5px;
                    padding-right: 5px;
                  }

                  .product-price {
                    display: flex;
                    justify-content: space-around;
                    margin-top: 5px;
                    margin-bottom: 5px;
                    /*span,*/
                    /*del {*/
                    /*}*/

                    span {
                      color: #f10215;
                    }
                  }


                  img {
                    width: 80%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }


</style>